@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.scroll-general {

}

.scroll-general:hover {
	text-shadow: 1px  1px     #FFFF66, 
    			-1px  1px     #FFFF66, 
    			 1px -1px     #FFFF66, 
    			-1px -1px     #FFFF66,
     			 1px  1px 5px #555; 
}

.ScrollNav {
	position: relative;
	top: 30vh;
	height: 30vh;
	width: 8vw;
}

.nav-selected {
	text-shadow: 1px  1px     #32CD32, 
    			-1px  1px     #32CD32, 
    			 1px -1px     #32CD32, 
    			-1px -1px     #32CD32,
     			 1px  1px 5px #555;
}

.nav-selected:hover {
	text-shadow: 1px  1px     #32CD32, 
    			-1px  1px     #32CD32, 
    			 1px -1px     #32CD32, 
    			-1px -1px     #32CD32,
     			 1px  1px 5px #555 !important;
}

.pointer {
	position: relative;
	top: 85px;
	height: 3px;
	width: 8vw;
	background-color: grey;
}

li {
	position: absolute;
	text-align: center;
}

ul {
	list-style-type: none;
	height: 100%;
}

.page-number {
	color: #808080;
	position: absolute;
	left: -20px;
}

.page-title {
	position: absolute;
}
.navigation-buttons {
  display: flex;
  flex-direction: column;
  height: 30%;
  position: relative;
  top: 88vh;
  z-index: 1;
  left: 95vw;
  cursor: pointer;
}

.back-button {
  z-index: 1;
  width: 50px;
}

.back-button-svg {
  fill: #2343F5;
}

.back-button-svg:hover {
  fill: #566EF7;
}

.next-button {
  z-index: 1;
  width: 50px;
  position: relative;
  left: 10px;
}

.next-button-svg {
  fill: #2343F5;
}

.next-button-svg:hover {
  fill: #566EF7;
}

.disabled {
  fill: #C0C0C0;
  cursor: default;
}

.disabled:hover {
  fill: #C0C0C0 !important; 
}
.interactive-page{
	background-color: #DDA0DD;
	height: 300px;
}

.svg-hint-button {
	fill: #BA55D3;
	stroke: #DA70D6;
	stroke-width: 0px;
}

.svg-hint-button-unclicked {
	text-decoration: blink;
	-webkit-animation-name: blinker;
	-webkit-animation-duration: 0.6s;
	-webkit-animation-iteration-count:infinite;
	-webkit-animation-timing-function:ease-in-out;
	-webkit-animation-direction: alternate;
}

@-webkit-keyframes blinker {
  from {opacity: 1.0;
  		stroke-width: 10px;}
  to {opacity: 0.0;
  		stroke-width: 20px;}
}

.animationBox {
	position: relative;
	left: 15vw;
	top: 10vh;
}

.controlButton {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 10px;
}

.replayButton {
	height: 50px;
	width: 50px;
	fill: #2343F5;
	cursor: pointer;
	margin-right: 10px;
}

.replayButton:hover {
	fill: #566EF7;
}

.pauseButton {
	height: 50px;
	width: 50px;
	fill: #2343F5;
	cursor: pointer;
	margin-left: 10px;
}

.pauseButton:hover {
	fill: #566EF7;
}
.image-slider {
	height: 100%;
	width: 100%;
}

.image-after {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50%;
  overflow: hidden;
}

.image-container {
	width: 100%;
	height: 100%;
}

.open-modal {
	position: relative;
	top: 10vh;
	left: 10vw;
	width: 24vh;
	height: 3vh;
	background-color: white;
	z-index: 2;
	cursor: pointer;
	border-radius: 5px;
}

.open-modal:hover {
	background-color: grey;
}

.popup-image {
	width: 90%;
	height: 90%;
}

.popup-content {
	display: flex;
	flex-direction: column;
	white-space: pre-line;
}

.popup-dialog {
	max-width: 80vw !important;
	width: 80vw !important;
	max-height: 80vh !important;
}


.content-slider {
  position: absolute;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.slider-icon {
	position: relative;
	height: 50px;
	width: 50px;
	top: 10%;
	left: -23px;
	cursor: col-resize;
}

.title {
	position: relative;
	font-weight: 500;
	font-size: 3vmin;
	text-transform: capitalize;
	letter-spacing: 0.01em;
	line-height: 1.2;
	color:#fff;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	top: 30vh;
}

.theme {
	position: relative;
	top: 20vh;
	font-weight: 900;
	font-size: 1.7vmin; 
	text-transform:uppercase;
	font-family:Poppins;
	letter-spacing: 10px;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	color: lightblue;
}

.images {
	height: 100vh;
	width: 60%;
	overflow: hidden;
	position: relative;
}

.text-content {
	position: relative;
	text-align: center;
	background-color: black;
	height: 100vh;
	width: 40%;
	text-align: left;
}

.description {
	position: relative;
	top: 30vh;
	font-size: 2vmin;
	line-height: 1.7;
	font-weight: 300;
	color:#fff;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	white-space: pre-line;
	
}

.slider-list {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
  	width: 100%;
}

.list-item {
	position: relative;
	height: 100%;
  	width: 100%;
  	overflow: hidden;
}

.old {
	position: relative;
	height: 100vh;
  	width: 100vw;
  	overflow: hidden;
  	display: flex !important; 
  	flex-direction: row;
}

.new {
	position: relative;
	height: 100vh;
  	width: 100vw;
  	overflow: hidden;
  	display: flex !important; 
  	flex-direction: row;
}

.vertical-separator {
	position: absolute;
	top: 0;
  	bottom: 0;
	height: 100%;
	width: 5px;
	background: grey;
	z-index: 1;
}
.scroll-nav {
  position: absolute;
  height: 0px;
  width: 0px;
  z-index: 1;
}

.content-right {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: horizontal;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

